.spotify-link {
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  display: -webkit-box;
  display: flex;
  -webkit-box-align: center;
          align-items: center;
  font-size: 24px;
  i {
    padding-top: 4px;
  }
}

.artist-header {
  display: -webkit-box;
  display: flex;
  -webkit-box-align: center;
          align-items: center;
  &__image-wrapper {
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
            align-items: center;
    margin-bottom: 1.5rem;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    margin-right: 1.5rem;
    background-color: #01BAEF;
    height: 100px;
    width: 100px;
    border-radius: 50%;
  }
  &__image {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.2), 0 4px 6px -2px rgba(0, 0, 0, 0.4);
    border-radius: 50%;
    -webkit-transform: translate(5px, -5px);
            transform: translate(5px, -5px);
  }
}

.song-details {
  margin-top: 1.5rem;
  padding: 1.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.6), 0 4px 6px -2px rgba(0, 0, 0, 0.6);
  &__spotify {
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.2), 0 4px 6px -2px rgba(0, 0, 0, 0.4);
    width: 300px;
  }
  &__youtube {
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.2), 0 4px 6px -2px rgba(0, 0, 0, 0.4);
  }
  &__edit {
    cursor: pointer;
  }
}

.song-edit {
  margin-top: 1.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.6), 0 4px 6px -2px rgba(0, 0, 0, 0.6);
  .form__body {
    padding: 1.5rem;
  }
  .form__buttons {
    display: -webkit-box;
    display: flex;
    text-align: center;
    .form-button {
      padding-top: 0.75rem;
      padding-bottom: 0.75rem;
      color: #fff;
      cursor: pointer;
      &--main[type="submit"] {
        background-color: #1EEAA9;
        -webkit-appearance: none;
        &:hover {
          box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.2), 0 4px 6px -2px rgba(0, 0, 0, 0.4);
          text-shadow: 2px 2px #01BAEF;
        }
      }
      &--secondary {
        background-color: #01BAEF;
        &:hover {
          text-shadow: 2px 2px #1EEAA9;
        }
      }
      &:hover {
        box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.2), 0 4px 6px -2px rgba(0, 0, 0, 0.4);
      }
      &:focus {
        box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.2), 0 4px 6px -2px rgba(0, 0, 0, 0.4);
        outline: none;
      }
    }
  }
}

%visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px; width: 1px;
  margin: -1px; padding: 0; border: 0;
}

.star-rating__star {
  display: inline-block;
  padding: 3px;
  vertical-align: middle;
  line-height: 1;
  font-size: 1.5em;
  background-color: #b3b3b3;
  color: transparent;
  text-shadow: 2px 2px 3px rgba(255,255,255,0.5);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  -webkit-transition: color .2s ease-out;
  transition: color .2s ease-out;
}

.star-rating__star:hover {
  cursor: pointer;
}

.star-rating__star.is-selected {
  color: #1EEAA9;
  text-shadow: none;
}

.star-rating__star.is-disabled:hover {
  cursor: default;
}

.star-rating__star__checkbox {
  @extend %visually-hidden;
}